import axios from "../config/axios";
import { useNavigate } from 'react-router-dom';
import { api } from "../config/api";
export default function useAuth() {

    const navigate = useNavigate();

    // waiting for a callback at /api_callback route and console.log the query params
    const handleCallback = async () => {
        // set window.location to the api callback url
        window.location = "http://pong.grademe.fr/api_callback";
        const { search } = window.location;
        const params = new URLSearchParams(search);
        const code = params.get('code');
        const state = params.get('state');
        console.log(code, state);
        const { data } = await axios.post(api.auth, { code, state });
        console.log(data);
        navigate('/dashboard');
    }

     const login = (data) => {

        axios.post(api.login, data)
            .then((res) => {
                console.log(res.data);
                localStorage.setItem('token', res.data.token);
                navigate('/dashboard');
            })
            .catch((err) => {
                console.log(err);
            })
    }
}
